import uniIcon from '../uni-icon/uni-icon.vue'
import uniBadge from '@/components/uni-badge/uni-badge.vue'
export default {
	name: 'uni-list-item',
	components: {
		uniIcon,
		uniBadge
	},
	data() {
		return {

		};
	},
	props: {
		title: {
			type: String,
			default: ''
		}, //列表标题
		note: {
			type: String,
			default: ''
		}, //列表描述
		disabled: { //是否禁用
			type: [Boolean, String],
			default: false
		},
		showArrow: { //是否显示箭头
			type: [Boolean, String],
			default: true
		},
		showBadge: { //是否显示数字角标
			type: [Boolean, String],
			default: false
		},
		showSwitch: { //是否显示Switch
			type: [Boolean, String],
			default: false
		},
		switchChecked: { //Switch是否被选中
			type: [Boolean, String],
			default: false
		},
		badgeText: String, //badge内容
		badgeType: { //badge类型
			type: String,
			default: 'success'
		},
		thumb: String, //缩略图
		showExtraIcon: { //是否显示扩展图标
			type: [Boolean, String],
			default: false
		},
		extraIcon: {
			type: Object,
			default () {
				return {
					type: 'contact',
					color: '#000000',
					size: 20
				};
			}
		}
	},
	methods: {
		onClick() {
			this.$emit('click')
		},
		onSwitchChange(e) {
			this.$emit('switchChange', e.detail)
		}
	}
}
